@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&family=Ubuntu:wght@300;400;700&display=swap);
:root {
  --primary-green: rgb(67, 167, 132);
  --primary-orange: rgb(222, 131, 62);
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: rgb(208, 58, 58);
  --primary-hover-red: red;
  --primary-light-grey: rgb(177, 177, 177);
}

body {
  font-family: 'Nanum Gothic', sans-serif !important;
}


/************************NAVBAR********************/
/*-----------menu links----*/

nav a.nav-link {
  font-weight: 500;
  letter-spacing: .0375px;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

/* nav bar customizatio   */
.navbar-nav > li > a {
  margin-right: .75rem;
  margin-left: .75rem;
  color: rgb(67, 167, 132) !important;
  color: var(--primary-green) !important;
  border-top: 0.3rem solid rgba(255, 255, 255, 0);
  border-top: 0.3rem solid var(--primary-dark)
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
border-top: 0.3rem solid rgb(222, 131, 62);
border-top: 0.3rem solid var(--primary-orange)
}

.navbar-brand{
  color: rgb(56, 171, 107) !important;
}

/* ********************HEADER********************* */

.header-wrapper{
  position: relative;
  background: url(/portfolio-landing-page/static/media/johannes-plenio-YRBPT5x5aLw-unsplash.69473784.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info{
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: 'Nanum Gothic', sans-serif;
  z-index: 1;
}

.main-info h1{
  /* color: aqua; */
  text-transform: uppercase;
}

/* ****************Typed Text*********** */

.typed-text{
  font-size: 2rem;
  color: whitesmoke;
}

/* ****************Header Main Offer Button*********** */

.btn-main-offer{
border: 1px solid rgb(67, 167, 132);
border: 1px solid var(--primary-green);
text-transform: uppercase;
border-radius:0 ;
padding: .75rem ;
color: rgb(203, 188, 165);
margin: 2rem;
}

.btn-main-offer:hover{
  background-color: red;
  color: white;
}



/* ********************About Me********************* */
.about-me-container{
  /* background-color: blanchedalmond; */
}

.photo-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

.profile-img {
  /* width: 16rem; */
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid rgb(222, 131, 62);
  border: 1px solid var(--primary-orange);
  
}

.about-header{
  color: rgb(222, 131, 62);
  color: var(--primary-orange);
  text-transform: uppercase;
  padding: 2rem;
  
}

@media(max-width: 468px) {
  .profile-img{
    width: 12rem;
  }
}


/* ********************SERIVCES**********/

.paragraph {
  padding: 2rem;
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;

}

.services{
  text-align:center;
  padding-bottom: 2rem;
  background: rgb(177, 177, 177);
  background: var(--primary-light-grey);
  height: 130vh;
}

.services .box{
  /* height: 16rem; */
  border-bottom: solid rgb(0,0,0);
  border-bottom: 0.3125rem solid rgb(222, 131, 62);
  border-bottom: 0.3125rem solid var(--primary-orange);
  padding: .625rem;
  margin-bottom: 1.875rem;
  background: rgb(255,255,255);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: rgb(222, 131, 62);
  background: var(--primary-orange);
  border-bottom: 0.3125rem solid rgb(0,0,0);
}

/* ********************Portfolio**********/

.projectContainer{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  padding-top: 4rem;
  background: url(/portfolio-landing-page/static/media/niceGif.21d0a45c.gif) no-repeat;
  background-size: cover;
  background-position: center;
  height: 130vh;
}
